import React, { useEffect, useRef, useState } from "react";
import cytoscape from "cytoscape";
import fcose from "cytoscape-fcose";
import { GraphNode, GraphEdge } from "../../types";

cytoscape.use(fcose);

interface GraphVisualizationProps {
  nodes: GraphNode[];
  edges: GraphEdge[];
  onNodeSelect: (nodeId: string) => void;
  onNodeHover: (nodeId: string | null) => void;
}

const GraphVisualization: React.FC<GraphVisualizationProps> = ({
  nodes,
  edges,
  onNodeSelect,
  onNodeHover,
}) => {
  const cyRef = useRef<HTMLDivElement>(null);
  const cyInstance = useRef<cytoscape.Core | null>(null);
  const tooltipRef = useRef<HTMLDivElement | null>(null);
  const [selectedNodeId, setSelectedNodeId] = useState<string | null>(null);
  // Red gradient colors for relatedness (dark to light)
  const relatednessColors = [
    "#8B0000", // Dark red (distance 0 - selected paper)
    "#B22222", // Fire brick (distance 1)
    "#DC143C", // Crimson (distance 2)
    "#FF6347", // Tomato (distance 3)
    "#FF7F7F", // Light coral (distance 4)
    "#FFB6C1", // Light pink (distance 5+)
  ];
  const clusterColors: Record<string, string> = {
    "NLP": "#4A90E2",
    "Computer Vision": "#7CB342",
    "Machine Learning": "#FB8C00", 
    "Default": "#666666",
  };

  const getNodeColor = (cluster?: string, distance?: number) => {
    // If we have a selected node, use relatedness colors
    if (selectedNodeId && distance !== undefined) {
      const colorIndex = Math.min(distance, relatednessColors.length - 1);
      return relatednessColors[colorIndex];
    }
    
    // Otherwise use cluster colors
    return cluster ? clusterColors[cluster] || clusterColors["Default"] : clusterColors["Default"];
  };

  // Calculate shortest path distances from selected node
  const calculateDistances = (selectedId: string) => {
    if (!cyInstance.current) return new Map();
    
    const distances = new Map<string, number>();
    const visited = new Set<string>();
    const queue = [{ nodeId: selectedId, distance: 0 }];
    
    distances.set(selectedId, 0);
    
    while (queue.length > 0) {
      const { nodeId, distance } = queue.shift()!;
      
      if (visited.has(nodeId)) continue;
      visited.add(nodeId);
      
      // Find connected nodes
      const node = cyInstance.current.getElementById(nodeId);
      const connectedEdges = node.connectedEdges();
      
      connectedEdges.forEach((edge: any) => {
        const connectedNode = edge.otherNode(node);
        const connectedId = connectedNode.id();
        
        if (!distances.has(connectedId) || distances.get(connectedId)! > distance + 1) {
          distances.set(connectedId, distance + 1);
          queue.push({ nodeId: connectedId, distance: distance + 1 });
        }
      });
    }
    
    return distances;
  };
  useEffect(() => {
    if (!cyRef.current) return;

    cyInstance.current = cytoscape({
      container: cyRef.current,
      elements: [
        ...nodes.map(node => ({
          data: {
            id: node.id,
            title: node.data.title,
            year: node.data.year,
            cluster: node.data.cluster,
            confidence: node.data.confidence,
          },
        })),
        ...edges.map(edge => ({
          data: {
            id: edge.id,
            source: edge.source,
            target: edge.target,
            type: edge.type,
          },
        })),
      ],
      style: [
        {
          selector: "node",
          style: {
            "background-color": (ele: any) => getNodeColor(ele.data("cluster")),
            "border-color": "#fff",
            "border-width": 2,
            label: "data(year)",
            "text-valign": "center",
            "text-halign": "center",
            width: 60,
            height: 60,
            "font-size": "12px",
            "font-weight": 600,
            color: "white",
          },
        },
        {
          selector: "node:hover",
          style: {
            "border-width": 3,
            "border-color": "#FFD700",
          },
        },
        {
          selector: "edge[type = \"citation\"]",
          style: {
            "line-color": "#888",
            "target-arrow-color": "#888",
            "target-arrow-shape": "triangle",
            width: 2,
          },
        },
        {
          selector: "edge[type = \"beats\"]",
          style: {
            "line-color": "#E74C3C",
            "target-arrow-color": "#E74C3C",
            "target-arrow-shape": "triangle",
            width: 3,
            "line-style": "dashed",
          },
        },
      ],
      layout: {
        name: "cose" as const,
        animate: true,
        animationDuration: 1000,
        fit: true,
        padding: 50,
      },
      minZoom: 0.3,
      maxZoom: 3,
      userPanningEnabled: true,
      userZoomingEnabled: true,
    });

    cyInstance.current.on("tap", "node", (evt) => {
      const nodeId = evt.target.id();
      setSelectedNodeId(nodeId);
      
      // Calculate distances and update node colors
      const distances = calculateDistances(nodeId);
      
      // Update all node colors based on relatedness
      cyInstance.current?.nodes().forEach((node: any) => {
        const id = node.id();
        const distance = distances.get(id) ?? 999;
        const newColor = getNodeColor(node.data("cluster"), distance);
        node.style("background-color", newColor);
      });
      
      onNodeSelect(nodeId);

    // Reset colors when clicking background
    cyInstance.current?.on("tap", (evt) => {
      // Only reset if clicking on background (not on a node)
      if (evt.target === cyInstance.current) {
        setSelectedNodeId(null);
        
        // Reset all node colors to cluster colors
        cyInstance.current?.nodes().forEach((node: any) => {
          const originalColor = getNodeColor(node.data("cluster"));
          node.style("background-color", originalColor);
        });
      }
    });    });
    // Create tooltip
    if (!tooltipRef.current) {
      tooltipRef.current = document.createElement("div");
      tooltipRef.current.style.position = "absolute";
      tooltipRef.current.style.zIndex = "1000";
      tooltipRef.current.style.background = "rgba(0, 0, 0, 0.9)";
      tooltipRef.current.style.color = "white";
      tooltipRef.current.style.padding = "12px 16px";
      tooltipRef.current.style.borderRadius = "8px";
      tooltipRef.current.style.fontSize = "14px";
      tooltipRef.current.style.pointerEvents = "none";
      tooltipRef.current.style.maxWidth = "300px";
      tooltipRef.current.style.boxShadow = "0 4px 20px rgba(0, 0, 0, 0.3)";
      tooltipRef.current.style.opacity = "0";
      tooltipRef.current.style.transition = "opacity 0.2s ease";
      document.body.appendChild(tooltipRef.current);
    }

    cyInstance.current.on("mouseover", "node", (evt) => {
      const node = evt.target;
      const data = node.data();
      
      if (tooltipRef.current) {
        const shortTitle = data.title.length > 50 ? data.title.substring(0, 50) + "..." : data.title;
        
        tooltipRef.current.innerHTML = 
          "<div style='font-weight: 600; color: #FFD700; margin-bottom: 8px;'>" + shortTitle + "</div>" +
          "<div style='margin-bottom: 4px;'><strong>Year:</strong> " + data.year + "</div>" +
          "<div style='margin-bottom: 4px;'><strong>Topic:</strong> " + (data.cluster || "Unknown") + "</div>" +
          "<div><strong>Confidence:</strong> " + data.confidence + "%</div>";
        
        const pos = node.renderedPosition();
        const container = cyRef.current?.getBoundingClientRect();
        
        if (container) {
          tooltipRef.current.style.left = (container.left + pos.x + 25) + "px";
          tooltipRef.current.style.top = (container.top + pos.y - 30) + "px";
          tooltipRef.current.style.opacity = "1";
        }
      }
      
      onNodeHover(evt.target.id());
    });
    cyInstance.current.on("mouseout", "node", () => {
      if (tooltipRef.current) {
        tooltipRef.current.style.opacity = "0";
      }
      onNodeHover(null);
    });
    return () => {
      if (cyInstance.current) {
        cyInstance.current.destroy();
      }
      if (tooltipRef.current && document.body.contains(tooltipRef.current)) {
        document.body.removeChild(tooltipRef.current);
        tooltipRef.current = null;
      }
    };  }, [nodes, edges, onNodeSelect, onNodeHover]);

  return (
    <div
      ref={cyRef}
      style={{
        width: "100%",
        height: "100%",
        border: "1px solid #e0e0e0",
        borderRadius: "12px",
        backgroundColor: "#fafafa",
        cursor: "grab",
        overflow: "hidden",
        boxShadow: "0 4px 12px rgba(0, 0, 0, 0.1)",
      }}
    />
  );
};

export default GraphVisualization;
